.pirsa-icon {
    background-repeat: no-repeat;
    background-size: 1.25rem auto; 
    background-position: center center;
    display: inline-block;
    width: 3em;
    height: 3em;    
    text-indent: -9999px;

    &__apple {
        background-image: url('../image/icon-apple.svg');
    }

    &__fish {
        background-image: url('../image/icon-fish.svg');
    }
    
    &__pig {
        background-image: url('../image/icon-pig.svg');
    }
    
    &__plant-leaf {
        background-image: url('../image/icon-plant-leaf.svg');
    }    

    &--rounded {
        background-color: white;
        border-radius: 50%;
        padding: 4px;
    }

    &--outline {
        border: 2px solid currentColor;
    }
}